  stroke-width: 1px; }
  .line--strokes:nth-child(1) {
    stroke: rgb(224,224,224);
    stroke-dashoffset: 7%; }
  .line--strokes:nth-child(2) {
    stroke: #FFF;
    stroke-dashoffset: 14%; }
  .line--strokes:nth-child(3) {
    stroke: #CCC;
    stroke-dashoffset: 21%; }
  .line--strokes:nth-child(4) {
    stroke: rgb(224,224,224);
    stroke-dashoffset: 28%; }
  .line--strokes:nth-child(5) {
    stroke: #eee;
    stroke-dashoffset: 35%; }

@keyframes stroke-offset {
  50% {
    stroke-dashoffset: 35%;
    stroke-dasharray: 0 87.5%; } }

.page-content {
  width: 100%;
  min-height: calc(100vh - 13px); }
  .page-content .content-wrap {
    position: relative;
    display: flex;
