    background-repeat: no-repeat;
    animation: flares 2s ease-in-out infinite alternate;
    backface-visibility: hidden;
    z-index: 1; }

.alien-planet {
  position: absolute;
  margin: 70px 0 auto;
  transform-style: preserve-3d;
  width: 600px;
  z-index: 6;
  will-change: transform, z-index;
  animation: orbitplanet 50s infinite linear;
  top: 0;
  height: 0; }
  .alien-planet .alien-planet__wrapper {
    width: 60px;
    height: 60px;
    background-image: url("/images/moon.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-color: transparent;
    animation-iteration-count: infinite;
    overflow: hidden;
    will-change: transform;
    animation: anti-spin 50s infinite linear; }
  .alien-planet .shadow {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background: transparent url("/images/moon-shadow.png") 0% 0% no-repeat;
    background-size: cover;
    border-radius: 100%;
    will-change: background-position;
    animation: shadow 50s infinite linear; }

.orbit--work {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -1; }
  .orbit--work .orbit__control {
    position: relative;
    margin: 0 auto;
    display: block; }
  .orbit--work .star-spin {
    position: absolute;
    width: 100%;
    height: 100%;
    will-change: transform;
    animation: planetRotate 100s infinite linear; }
  .orbit--work .blackhole {
    position: absolute;
    width: 520px;
    height: 520px;
    border: none;
    outline: 1px solid transparent;
    background: url("/images/blackhole.png");
    background-size: contain;
    background-repeat: no-repeat;
    animation: suck 2s ease-in-out infinite alternate;
    backface-visibility: hidden;
    z-index: 1; }

